import "./NewsCard.scss"
import React from "react"

class NewsCard extends React.Component {
    constructor() {
        super()

        this.state = {
        }
    }

    render() {
        const styles = {
            background: this.props.color
          }
        return(
            <div className="newsCard" style={styles}>
                <img loading="lazy" className="cardImage mx-auto d-block" src={this.props.urlImagen} alt="imagen"/>
                <div className="cardContenido p-3">
                    <h3 className="cardSubtitle text-center">{this.props.subtitulo}</h3>
                    <h4 className="cardSubtitle2 text-center text-white">{this.props.subtitulo2}</h4>
                    <p className="cardParagraph text-center">{this.props.parrafo}</p>
                    <p className="cardParagraph2 text-center">{this.props.parrafo2}</p>
                    <div className="d-flex justify-content-center">
                        <button type="button" className="cardButton text-white border-0 p-2">Read More</button>
                    </div>
                </div>          
            </div>
        )
    }
}

export default NewsCard